<template>
	<section>
		<div class="inr-banner">
			<img :src="this.imgUrlBanner" alt=""/>
		</div>
		<div class="inner">
		       <div class="inf-info">当前位置：<a href="">首页 &gt;</a><a href="javascript:;">&gt;</a><span class="inf-info-tet">公告</span></div>
		       <div class="inf-content clearfix">
			         <div class="inf-cont-left activeLeft">
			           <div class="cont-left-title">{{noticedata.title}}</div>
			           <!--<div class="left-txt-tiao clearfix spac">
			              <span class="nr-ico-txt"><i class="c-ico inf-nr-ico"></i>作者：{{conten.author}}</span>
			              <span class="nr-ico-txt"><i class="c-ico inf-nr-icol"></i>浏览次数：{{conten.browseCount}}</span>
						  <span class="nr-ico-txt" v-if="conten.origin != null && conten.origin != ''"><i class="c-ico inf-nr-icoy"></i>来源：{{conten.origin}}</span>
			              <span class="nr-ico-txt"><i class="c-ico inf-nr-icor"></i>{{conten.releaseDate}}</span>
			           </div>-->  
			            <div class="cont-left-text" style="overflow:hidden;width:95%" v-html="noticedata.contentWithHtml">
	            		</div>
			         </div>
			        
		       </div>
	    </div>
	   
	</section>
</template>

<script>
	import { noticeData,noticeList,queryCarousels } from  '../../api/info'
	export default{
		data(){
			return{
				noticedata:{},
				uuid:'',
				imgUrlBanner:'',
			}
		},
		methods:{
			dataList(){
				var param ={
					pageSize:10,
					pageNum:1
				}
				noticeList(param).then(res =>{
					this.uuid = res.datas[0].uuid
					var param ={
						uuid:res.datas[0].uuid
					}
					noticeData(param).then(res =>{
						this.noticedata = res.dataList
						
					})
				})
			},
			getBanner(){
				queryCarousels({typeCode:"WEB",pageCode:"zx_index_barner",pageNum:1,pageSize:10}).then((res)=>{
					
					this.imgUrlBanner = res.datas[0].imageUrl
					
				});
			}
			
		},
		mounted(){
			this.getBanner()
			this.dataList()
		}
	}
</script>

<style scoped>
	.inf-cont-left{width: 100% !important;}
	.cont-left-title{text-align: center;}
	
</style>


